<!--银行活动-->
<template>
  <div>
    <el-card>
      <el-button :type="step1" id="btn_step1" class=" btn-arrow-right ">基本信息设置</el-button>
      <span class="btn-link"></span>
      <el-button :type="step2" id="btn_step2" class="btn-arrow-right ">优惠券规则设置</el-button>
      <span class="btn-link"></span>
      <el-button :type="step3" id="btn_step3" class="btn-arrow-right">商户设置</el-button>
    </el-card>
    <div>
      <el-card>
        <el-form v-if="showfrom" class="account-plat-formitem" :model="messageForm" :disabled="disabled"
          :rules="messageFormrules" ref="messageForm" label-width="120px"
          style="text-align: left;font-size:20px;font-weight: bold;margin-top: 10px;width: 80%">
          <el-row :gutter="24">
            <el-col :md="24">
              <div>活动信息设置</div>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="height: 10px;width: 10px;">
          </el-row>
          <el-row :gutter="24">
            <el-col :md="12">
              <el-form-item label="活动主标题:" label-width="105px" prop="bai_m_caption">
                <el-input show-word-limit :maxlength="9" placeholder="请输入内容,最多支持9个字"
                  v-model="messageForm.bai_m_caption"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="活动副标题:" prop="bai_s_caption">
                <el-input show-word-limit :maxlength="9" placeholder="请输入内容,最多支持9个字"
                  v-model="messageForm.bai_s_caption"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="24" style="margin-bottom: 15px">
              <el-form-item label="活动内容:" prop="bai_describer" label-width="105px">
                <el-input show-word-limit style="margin-bottom: 20px;width: 100%" type="textarea" autosize
                  :maxlength="150" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入内容,最多支持150个字"
                  v-model="messageForm.bai_describer">
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="12">
              <el-form-item label="活动时间:" prop="bai_start_time" label-width="105px">
                <el-date-picker :editable="false" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="开始时间"
                  v-model="messageForm.bai_start_time" style="width: 100%;margin-right: 20px">
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item prop="bai_end_time" label="到" class="time1">
                <el-date-picker :editable="false" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="结束时间"
                  v-model="messageForm.bai_end_time" style="width: 100%">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <!-- 小程序分享页面背景图片设置 -->
          <el-row :gutter="24">
            <el-col :md="12" style="display: flex;align-items: center;">
              <div>
                <el-form-item prop="sbg_img" label="小程序分享页面背景图片设置" label-width="210px" />
                <div style="margin-top:-34px;width: 200px;text-align: center;">
                  <el-image style="width: 50px; height: 50px"
                    :src="require('../../../../assets/images/upload-plus.png')">
                  </el-image> <br>
                  <div @click="share_background('1')" class="btn">选择图片
                  </div>
                  <div style="font-size: 12px;margin-top: 6px;">如不上传，使用系统默认图片</div>
                </div>
              </div>
              <div style="width: 100px; margin: 0 10px;min-height: 20px;"><el-image :src="sbg_img" v-if="sbg_imgIf" /></div>
              <div @click="previewShow=true" class="btn" style="color: #000;">分享效果预览 <i class="el-icon-view"></i></div>
            </el-col>
          </el-row>
        </el-form>

        <!-- 系统库背景图片弹窗 -->
        <el-dialog :visible.sync="shareShow" width="1200px">
          <div class="shareNav">
            <div class="btn" style="font-size: 22px;" @click="share_background('1')">系统库</div>
            <div class="btn" style="font-size: 22px;" @click="share_background('2')">本地库</div>
          </div>
          <shareBackground @preservation="preservation" ref="shareRef" :type="1" :shareMsg='shareMsg' :upload="false"
            style="width: 100%;height: 500px;display: block;" />
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="preservation(false)">保 存</el-button>
          </span>
        </el-dialog>
        <!-- 预览选中效果图 -->
        <el-dialog :visible.sync="previewShow" width="1200px" >
          <div style="display: flex;justify-content: space-around;height: 680px;">
            <div>
              <el-card :body-style="{ padding: '0px' }" class="box_card">
                <el-image :src="require('../../../../assets/images/wxbg.jpg')" class="image" />
                <div class="left-box">
                  <el-image :src="require('../../../../assets/images/wxbj2.jpg')" class="image" />
                  <!-- 选中图片 -->
                  <!-- <el-image :src="require('../../../../assets/images/content.png')" class="imageContent1" /> -->
                </div>
              </el-card>
              <div style="margin-top: 10px;font-size: 26px;font-weight: 700;">
                分享缩略图效果
              </div>
            </div>
            <div>
              <el-card :body-style="{ padding: '0px' }" class="box_card">
                <el-image :src="sbg_img" class="image" />
                <el-image :src="require('../../../../assets/images/shareBtn.png')" class="imageBtn" />
                <el-image :src="require('../../../../assets/images/content.png')" class="imageContent" />
                <div class="previewNav">{{messageForm.bai_m_caption}}</div>
                <div class="previewStartTime">开始时间：{{messageForm.bai_start_time}}</div>
                <div class="previewEndTime">结束时间：{{messageForm.bai_end_time}}</div>
                <div class="previewContent">{{messageForm.bai_describer}}</div>
                <div class="previewCompany"></div>
              </el-card>
              <div style="margin-top: 10px;font-size: 26px;font-weight: 700;">
                点开后跳转页效果
              </div>
            </div>
          </div>
        </el-dialog>


        <div style="margin: 50px 0 0 -13%">
          <el-button style="margin: 0 10px" v-if="messageForm_button" @click="next1('messageForm')">下一步</el-button>
          <div v-if="messageForm_button" style="height: 100px;width: 100%"></div>
        </div>
        <!--form 2-->
        <el-form v-if="showfrom2" class="account-plat-formitem" :model="Coupon_ruleForm" :disabled="disabled"
          :rules="Coupon_ruleFormrules" ref="Coupon_ruleForm" label-width="120px"
          style="text-align: left;font-size:20px;font-weight: bold;margin-top: 10px">
          <el-row :gutter="24">
            <el-col :md="24">
              <div>优惠券领取与使用规则</div>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="24">
