                <el-radio v-model="radio" label="1" style="position: absolute;top: 13px;left: 10px;"
                  @change="radio_id1()">指定客户参与</el-radio>
                <el-radio v-model="radio" label="2" style="position: absolute;top: 45px;left: 10px;"
                  @change="radio_id2()">任意客户均可参与
                </el-radio>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="24" style="margin-top: 40px" v-if="this.Coupon_ruleForm.bai_user_scope !== '1'">
            <el-col :md="5">
              <el-form-item label="手机号码:" prop="bau_guest_phone">
                <el-input style="width: 150px" :maxlength="11" v-model="Coupon_ruleForm.bau_guest_phone"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="5">
              <el-form-item label="次数:" prop="bau_get_num">
                <el-input style="width: 50px;" :maxlength="2" v-model="Coupon_ruleForm.bau_get_num"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="2">
              <el-button type="primary" @click="phone_Add('Coupon_ruleForm')">录入</el-button>
            </el-col>
            <el-col :md="2">
              <el-upload ref="uploadFile" :action="this.uploadConfig.actionUrl" :with-credentials='true'
                :show-file-list="true" :limit="1" accept=".xlsx" :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload" :on-exceed="selUploadFile">
                <el-button type="primary">导入</el-button>
              </el-upload>
            </el-col>
            <el-col :md="2">
              <el-button type="primary">
                <el-link style="text-decoration: none;color: #FFFFFF" :href="href">模板下载</el-link>
              </el-button>
            </el-col>
          </el-row>


          <el-table v-if="this.Coupon_ruleForm.bai_user_scope !== '1'" :data="tableData_phone" @sort-change="sortColumn"
            style="margin-left: 120px;width: 60%" class="account-plat-fixed-right account-plat-fixed " border
            ref="platformUserTable" tooltip-effect="dark" :row-style="{'height':'10px'}"
            :cell-style="{'padding':'5px 0px'}"
            :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }">
            <el-table-column fixed align="center" type="index" label="序号" width="50">
            </el-table-column>
            <el-table-column prop="bau_guest_phone" label="手机号码" align="center" show-overflow-tooltip width="150">
            </el-table-column>
            <el-table-column prop="bau_get_num" label="次数" align="center" show-overflow-tooltip width="150">
            </el-table-column>
            <el-table-column prop="bau_writetime" label="录入时间" align="center" show-overflow-tooltip width="150">
            </el-table-column>
            <el-table-column prop="cui_login_id" label="操作员" align="center" show-overflow-tooltip width="150">
            </el-table-column>
            <el-table-column fixed="right" label="操作" min-width="150" align="center">
              <template slot-scope="scope">
                <el-button @click="phone_Delete(scope.$index, scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination v-if="this.Coupon_ruleForm.bai_user_scope !== '1'" style="margin-left:300px;margin-top: 20px"
            background :page-sizes="[10, 20, 50, 100,200]" layout="total, sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange_phone" @current-change="handleCurrentChange_phone"
            :current-page="queryParam.currentPage" :page-size="queryParam.pageSize" :total="queryParam.totalRow">
            >
          </el-pagination>


          <el-row :gutter="24" style="margin-top: 20px">
            <el-col :md="24">
              <el-form-item label="客户领取方式:" style="position: relative">
                <el-radio v-model="radio1" label="1" style="position: absolute;top: 13px;left: 10px;"
                  @change="radio1_id4()">卡激活领取</el-radio>
                <el-radio v-model="radio1" label="2" style="position: absolute;top: 45px;left: 10px;"
                  @change="radio1_id1()">直接领取</el-radio>
                <el-form-item label="指定银行卡类型:" style="margin-left: 150px;position: relative" v-if="way_show">
                  <el-checkbox-group v-model="checkedWeek" @change="Check_box_button">
                    <el-checkbox label="3">储蓄卡</el-checkbox>
                    <el-checkbox label="2">信用卡</el-checkbox>
                  </el-checkbox-group>
                  <el-form-item prop="bai_get_flag_money">
                    <div style="position: absolute;top: -48px;left: 200px;font-weight: normal">支付金额
                      <el-input @change="fixedChange('bai_get_flag_money')" style="width: 100px;margin: 0 10px;"
                        v-model="Coupon_ruleForm.bai_get_flag_money"></el-input>
                      元
                    </div>
                  </el-form-item>
                </el-form-item>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="24">
              <el-form-item label-width="220px" label="同一客户优惠券领取数量限制:" style="margin-top: 20px" prop="bai_get_num_area">
                <div style="font-weight: normal;display: flex;align-items: center;">活动期内限领
                  <el-input style="width: 100px;margin: 0 10px" v-model="Coupon_ruleForm.bai_get_num_area"
                    @change="inp_but"></el-input>
                  张 <el-tooltip class="item" effect="dark" placement="top">
                    <div style="width: 380px;line-height: 26px;" slot="content">{{contentItem1}}<br />{{contentItem2}}
                    </div>
                    <div
                      style="margin: 0 0 0 10px;color: red;font-weight: 700; width: 20px;height:20px;border-radius: 50%;background-color: rgb(144, 147, 153);line-height: 20px;text-align: center;cursor: pointer;">
                      ?</div>
                  </el-tooltip>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="24" style="position: relative">
              <el-form-item label-width="165px" label="优惠券可用时间设置:">
                <el-radio v-model="radio3" label="1" style="position: absolute;top: 13px;left: 10px;"
                  @change="radio3_id1()">活动期内任意时间可用
                </el-radio>
                <el-radio v-model="radio3" label="2" style="position: absolute;top: 50px;left: 10px;"
                  @change="radio3_id2()">活动期内，客户领券后
                </el-radio>
              </el-form-item>
              <el-form-item v-if="radio3=='2'" prop="bai_ticket_use_time_area1"
                style="position: absolute;top: 37px;left: 235px">
                <div style="font-weight: normal;">
                  <el-input v-if="radio3=='2'" style="width: 100px;margin: 0 10px;"
                    v-model="Coupon_ruleForm.bai_ticket_use_time_area1"></el-input>
                  天内可用
                </div>
              </el-form-item>
              <el-form-item v-if="radio3!=='2'" style="position: absolute;top: 37px;left: 235px">
                <div style="font-weight: normal;">
                  <el-input style="width: 100px;margin: 0 10px;" :disabled="true"></el-input>
                  天内可用
                </div>
              </el-form-item>
              <div v-show="text_show"
                style="margin-top: 50px;font-weight: normal;font-size: 12px;color: #a39292;margin-left: 170px">
                用户在活动有效期内领取优惠券，则从领取当天开始计算天数无论用户何时领取优惠券，优惠券在活动有效期结束后均不可用
              </div>
            </el-col>
          </el-row>
        </el-form>
        <div style="margin: 50px 0 0 -13%">
          <el-button style="margin: 0 10px" v-if="Coupon_ruleForm_button1" @click="next2()">上一步</el-button>
          <el-button style="margin: 0 10px" v-if="Coupon_ruleForm_button2" @click="next3('Coupon_ruleForm')">下一步
          </el-button>
          <div v-if="Coupon_ruleForm_button1" style="height: 100px;width: 100%"></div>
        </div>


        <!--form 3-->
        <el-form v-if="showfrom3" class="account-plat-formitem" :model="CommercialForm" :disabled="disabled"
          :rules="CommercialFormrules" ref="CommercialForm" label-width="120px"
          style="text-align: left;font-size:20px;font-weight: bold;margin-top: 10px">
          <el-row :gutter="24">
            <el-col :md="24">
              <div>商户设置</div>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-top: 20px">
            <el-col :md="24">
              <el-form-item label="商户报名活动的方式:" label-width="180px">
                <el-radio v-model="radio" label="1">商家选择银行预设标准券，报名参与活动</el-radio>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="24">
              <el-form-item label-width="180px">
                <div style="margin: -30px 0;font-size: 16px">标准券设置</div>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="12">
              <el-form-item label="优惠券金额：" label-width="275px" prop="bmti_money_1">
                <el-input @change="fixedChange1('bmti_money_1')" style="width: 200px;margin-right: 10px;"
                  v-model="CommercialForm.bmti_money_1"></el-input>元
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="使用门槛：" label-width="95px" prop="bmti_money_2">
                <el-input @change="fixedChange1('bmti_money_2')" style="width: 200px;margin-right: 10px;"
                  v-model="CommercialForm.bmti_money_2"></el-input>元
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <!--              <el-col :md="6">-->
            <!--                <el-form-item label="标准优惠券总投放量：" label-width="275px" prop="bmti_num_count">-->
            <!--                  <el-input style="width: 200px" v-model="CommercialForm.bmti_num_count"></el-input>-->
            <!--                </el-form-item>-->
            <!--              </el-col>-->

            <!-- <el-col :md="24">
                  <el-form-item label="优惠券类型:" label-width="270px" prop="bmti_use_area">
                    <el-select style="width: 200px;margin-left: 5px" placeholder="请选择" v-model="CommercialForm.bmti_use_area"
                    >
                      <el-option
                        v-for="item in option"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col> -->
          </el-row>
          <el-row :gutter="24">
            <el-col :md="24">
              <el-form-item label-width="180px" label="优惠券投放数量:" prop="bai_num_count">
                <el-input style="width: 200px;margin-right: 10px;" v-model="CommercialForm.bai_num_count"></el-input>张
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :md="24">
              <el-form-item label="可报名活动的商户范围:" label-width="180px">
                <el-radio v-model="radio4" label="1">指定商户</el-radio>
                <el-button @click="Select_merchant1()">选择商户</el-button>
              </el-form-item>
              <div style="margin-top: 20px;">
                <el-table ref="tableData3" class="new-table-fixed-right new-table-fixed" tooltip-effect="dark"
                  :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}" :data="tableData3"
                  :row-key="getRowKey" @sort-change="sortColumn" border style="width: 50%;margin-left: 10%"
                  :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }">

                  <el-table-column fixed align="center" type="index" label="序号" width="50">
                  </el-table-column>
                  <el-table-column prop="bmi_mer_name" label="商户名称" show-overflow-tooltip align="center" width="100">
                  </el-table-column>
                  <el-table-column prop="bii_name" label="所属行业" show-overflow-tooltip align="center" width="100">
                  </el-table-column>
                  <el-table-column prop="bmi_license_code" label="统一社会信用代码" show-overflow-tooltip align="center"
                    width="100">
                  </el-table-column>
                  <el-table-column prop="bsi_name" label="入住机构" show-overflow-tooltip align="center" width="100">
                  </el-table-column>
                  <el-table-column fixed="right" label="操作" min-width="100" align="center">
                    <template slot-scope="scope">
                      <el-button @click="shop_delete(scope.$index, scope.row)">删除</el-button>
                    </template>
                  </el-table-column>
                </el-table>
                <el-pagination style="margin:20px 0 0 20%;" background :page-sizes="[10, 20, 50, 100,200]"
                  layout="total, sizes, prev, pager, next, jumper" @size-change="handleSizeChange_shop2"
                  @current-change="handleCurrentChange_shop2" :current-page="queryParam2.currentPage"
                  :page-size="queryParam2.pageSize" :total="queryParam2.totalRow">
                </el-pagination>
              </div>
            </el-col>
          </el-row>
        </el-form>
        <div style="margin: 50px 0 0 -13%">
          <el-button style="margin: 0 10px" v-if="CommercialForm_button1" @click="next4()">上一步</el-button>
          <el-button style="margin: 0 10px" type="primary" v-if="CommercialForm_button2"
            @click="next5('CommercialForm')">保存到草稿箱</el-button>
          <el-button style="margin: 0 10px" type="primary" v-if="CommercialForm_button3"
            @click="next6('CommercialForm')">发布</el-button>
          <el-button style="margin: 0 10px" v-if="CommercialForm_button4" @click="next7()">取消</el-button>
          <div v-if="CommercialForm_button1" style="height: 100px;width: 100%"></div>
        </div>
      </el-card>
      <el-dialog :show-close="false" :visible.sync="dialogVisible" width="90%">
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ '待选商户' }}</span>
        </div>
        <div class="pop-up">
          <el-form :model="shopForm">
            <el-row :gutter="12">
              <el-col :md="4">
                <el-form-item label="商户所属行业:" label-width="100px" prop="bmi_industry_id">
                  <el-select style="width: 150px" clearable placeholder="请选择" v-model="shopForm.bmi_industry_id">
                    <el-option v-for="item in tableData_industry" :key="item.bii_id" :label="item.bii_name"
                      :value="item.bii_id">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :md="6">
                <el-form-item label="商户名称:" label-width="160px" prop="bmi_mer_name">
                  <el-input v-model="shopForm.bmi_mer_name"></el-input>
                </el-form-item>
              </el-col>
              <el-col :md="4">
                <el-button type="primary" style="margin-left: -80px" @click="merchants_query()">查 询</el-button>
              </el-col>
            </el-row>
