        if (this.current === 1) {
          this.step1 = "success";
          this.step2 = "info";
          this.step3 = "info";
        } else if (this.current === 2) {
          this.step1 = "success";
          this.step2 = "success";
          this.step3 = "info";
        } else if (this.current === 3) {
          this.step1 = "success";
