  }

  .shareNav {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    padding: 15px 20px;
  }

  .btn {
    color: rgb(0, 162, 254);
    font-size: 12px;
    text-decoration: underline;
    cursor: pointer;
  }

  .box_card {
    width: 368px;
    height: 644px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .image {
    width: 100%;
    /* height: 100%; */
    left: 0;
    top: 0;
    position: absolute;
  }

  .left-box {
    position: absolute;
    width: 224px;
    height: 178px;
    top: 190px;
    left: 78px;
    overflow: hidden;
  }

  .imageBtn {
    width: 120px;
    position: absolute;
    left: calc(50% - 60px);
    bottom: 70px;
  }

  .imageContent {
    width: 280px;
    position: absolute;
    left: calc(50% - 140px);
    top: 60px;
  }

  .imageContent1 {
    width: 140px;
    position: absolute;
    left: calc(50% - 70px);
    top: 36px;
  }
  .previewNav{
    width: 100%;
    position: absolute;
    left: 0px;
    top: 80px;
    font-size: 22px;
    font-weight: 700;
  }
  .previewStartTime{
    width: 236px;
    position: absolute;
    left: 68px;
    top: 216px;
    text-align: left;
    height: 20px;
    font-size: 10px;
  }
  .previewEndTime{
    width: 236px;
    position: absolute;
    left: 68px;
    top: 232px;
    text-align: left;
    height: 20px;
    font-size: 10px;
